<template>
  <div>
    <v-row class="mb-8">
      <v-col xl="3" lg="4" md="6" sm="12">
        <ImageFrame image_url="/img/me.webp" style="width: 100%; height: 100%" class="ma-2" alt_text="An image of Jonathan Law"></ImageFrame>
      </v-col>

      <v-col xl="9" lg="8" md="6" sm="12">
        <p class="display-1">Hi there !</p>
        <p>I am <span style="color: #00A56A; font-size: 110%;" class="font-weight-bold">Jonathan Law</span>, a full stack developer
          based in Malaysia. This website is still being built as we are speaking, so few pages will be missing some contents :(
        </p>
        <p class="mt-8 font-weight-bold whack_intro" id="whack_intro_block" v-text="intros[current_intro] "></p>
        <p class="mt-10 text-right font-weight-light">{{ cat_fact }}<br>(catfact.ninja, 2020)</p>
      </v-col>
    </v-row>

    <Skills></Skills>

  </div>
</template>

<script>
  export default {
    name: 'Home',
    components: {
      Skills: () => import('../components/Skills'),
      ImageFrame: () => import('../components/ImageFrame')
    },
    data: () => ({
      age: 22,
      cat_fact: "",
      intros: [
        "I CODE PROGRAMS",
        "I CREATE ROBOTS",
        "I GIVE THEM EYES",
        "I SPIN WEBS",
        "I GRAB CAT FACTS",
      ],
      current_intro: 0
    }),
    mounted() {
      let d = new Date();
      this.age = d.getFullYear() - 1998;

      fetch("https://catfact.ninja/fact")
              .then(r => r.json()).then((r) => { this.cat_fact = r.fact.replace("/", "").replace('"', "") })


      let iter = 1;
      let whack_block = document.getElementById("whack_intro_block");
      setInterval(() => {
        whack_block.classList.remove("whack_intro");
        this.current_intro = iter;
        iter = (iter === this.intros.length - 1) ? 0 : iter + 1;
        void whack_block.offsetWidth;
        whack_block.classList.add("whack_intro");
      }, 3000)
    }
  }
</script>

<style scoped>
  .whack_intro {
    font-size: 150%;
    overflow: hidden;
    border-right: .15em solid orange;
    white-space: nowrap;
    letter-spacing: .1em;
    display: inline-block;
    max-width: max-content;
    animation:
            typing 3s steps(20, end),
            blink-caret .75s step-end infinite;
  }

  @keyframes typing {
    0% { width: 0; }
    5% { width: 0; }
    50% { width: 100%; }
    95% { width: 0; }
    100% { width: 0; }
  }

  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
  }
</style>
